<template>
	<div class="sub-container">
		<h2 class="board-title">{{ board.title }}</h2>
		<div class="board-container">
			<div class="board-header">
				<div class="board-info">
					<span class="type">작성일</span>
					<span class="data"
						>{{ $moment(board.createDate).format('YYYY.MM.DD') }}
					</span>
				</div>
				<div class="board-info">
					<span class="type">작성자</span>
					<span class="data">관리자</span>
				</div>
			</div>
			<div class="board-content ck-content" v-html="board.contents"></div>
		</div>
	</div>
</template>

<script>
import { fetchNoticeDetail } from '@/api/notice';

export default {
	created() {
		let id = this.$route.params.id;
		this.fetchNoticeDetail(id);
	},
	data() {
		return {
			isLoading: false,
			board: {},
		};
	},
	methods: {
		async fetchNoticeDetail(id) {
			const { data } = await fetchNoticeDetail(id);
			this.board = data;
		},
	},
};
</script>

